@font-face {
    font-family: "PretendardRegular";
    src: url(../font/Pretendard-Regular.subset.woff);
}

@font-face {
    font-family: "PretendardMedium";
    src: url(../font/Pretendard-Medium.subset.woff);
}

@font-face {
    font-family: "PretendardSemiBold";
    src: url(../font/Pretendard-SemiBold.subset.woff);
}


html,
body {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    border: 0;
}

.wrap {
    width: 100%;
    height: 100%;
    background-color: #444;
    font-family: "PretendardRegular";
    overflow: hidden;
}

.movie_container {
    position: relative;
    display: flex;
    justify-content: center;
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    padding: 0 15px;
}

.movie_top_title {
    margin: 0;
    padding: 0;
    margin-top: 5%;
    font-size: 50px;
    color: #fff;
}

.movie_list {
    width: 500px;
    height: 100%;
    margin-left: 5%;
    box-sizing: border-box;
    padding: 15px;
    background-color: #fff;
    box-shadow: 0 0 5px 3px rgba(0,0,0,0.2);
    overflow: auto;
}

@media screen and (max-width: 850px) {
    .movie_container {
        flex-direction: column;
        align-items: center;
    }

    .movie_list {
        width: 100%;
        margin-left: 0;
    }
}