.movie {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    background-color: #e9e9e9;
    text-align: center;
    box-sizing: border-box;
    padding: 15px;
}

.movie > div:first-child {
    flex: none;
    width: 30%;
    margin-top: -50px;
}

.movie > div:first-child img {
    width: 100%;
    box-shadow: 0 0 3px 1px rgba(0,0,0,0.5);
    transition: 0.5s;
}

.movie:hover > div:first-child img {
    transform: scale(1.1);
}

.movie > div:nth-child(2) {
    padding: 15px 0;
}

.movie > div:nth-child(2) > h2 {
    margin-bottom: 15px;
}

.movie > div:nth-child(2) > ul {
    display: flex;
    justify-content: center;
    margin-top: 15px;
}

.movie > div:nth-child(2) > ul > li {
    margin: 0 10px;
}